import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss']
})
export class ProductsComponent implements OnInit {

  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    public vhComponent: VhComponent
  ) { }

  ngOnInit(): void {
  }
  gotoProductsList() {
    if (this.vhAuth.checkMyPermission("product_enable_manage_product")) this.router.navigate(['/sales/dashboard/manage/products/products-list']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoProductCategory() {
    if (this.vhAuth.checkMyPermission("product_enable_manage_product_category")) this.router.navigate(['/sales/dashboard/manage/products/product-category']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoServicesList() {
    if (this.vhAuth.checkMyPermission("product_enable_manage_service")) this.router.navigate(['/sales/dashboard/manage/products/services-list']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoServiceCategory() {
    if (this.vhAuth.checkMyPermission("product_enable_manage_service_category")) this.router.navigate(['/sales/dashboard/manage/products/service-category']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoCombo() {
    if (this.vhAuth.checkMyPermission('product_enable_manage_combo')) {
      this.router.navigate(['/sales/dashboard/manage/products/combo']);
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  gotoComboCategory(){
    if (this.vhAuth.checkMyPermission('product_enable_manage_combo')) {
      this.router.navigate(['/sales/dashboard/manage/products/combo-category']);
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  gotoPrintBarcodeQick(){
    this.router.navigate(['/sales/dashboard/manage/products/print-barcode']);
  }
}
