import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProductsComponent } from './products.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { ComboCategoryComponent } from './combo-category/combo-category.component';

const routes: Routes = [
  {
    path: '',
    component: ProductsComponent
  },
  {
    path: 'products-list',
    loadChildren: () => import('./products-list/products-list.module').then(m => m.ProductsListModule)
  },
  {
    path: 'product-category',
    loadChildren: () => import('./product-category/product-category.module').then(m => m.ProductCategoryModule)
  },
  {
    path: 'services-list',
    loadChildren: () => import('./services-list/services-list.module').then(m => m.ServicesListModule)
  },
  {
    path: 'service-category',
    loadChildren: () => import('./service-category/service-category.module').then(m => m.ServiceCategoryModule)
  },
  {
    path: 'combo',
    loadChildren: () => import('./combo/combo.module').then(m => m.ComboModule)
  },
  {
    path: 'combo-category',
    loadChildren: () => import('./combo-category/combo-category.module').then(m => m.ComboCategoryModule)
  },
  {
    path: 'print-barcode',
    loadChildren: () => import('./print-barcode/print-barcode.module').then(m => m.PrintBarcodeModule)
  },
]

@NgModule({
  declarations: [ProductsComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class ProductsModule { }
